.grid-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  min-width: 200px;

  border: #79817e 10px solid;
}

.save-load-buttons {
  margin-top: 10px;

}

.save-load-buttons button {
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 8px 16px;
  font-size: 16px;
}

.grid-wrapper {
  position: relative; /* For absolute positioning of moving-bar */
  display: inline-block; /* Shrink to fit the grid */
}

.grid {
  display: flex; /* Changed from inline-flex to flex for better control */
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  /* Removed width: fit-content to allow grid-wrapper to wrap it */
}

.row {
  display: flex;
}

.clear-button {
  margin-top: 20px;
}

.cell {
  width: 10px;
  height: 10px;
  border: 1px solid #ddd;
  background-color: white;
}

.cell.filled {
  background-color: black;
}

.cell.border {
  background-color: rgb(62, 176, 66);
}

.cell.error {
  background-color: rgb(255, 30, 0);
}

.cell.empty {
  background-color: white;
}

.parameters {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.clearDiv {
  width: 50%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.sliderDiv {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.grid-size-parameter,
.brush-size-parameter {
  width: 100%;
  margin-top: 10px;
}

.clear-button-button {
  padding: 5px 10px;
  font-size: 16px;
}

.grid-size-parameter {
  margin-top: 10px;
}

/* Moving Bar Styles */
.moving-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 95%;
  top: 2.5%;



  /* Removed border to prevent overlapping with grid's border */
  box-sizing: border-box;
  background-color: rgba(255, 0, 0, 0); /* Transparent */
  pointer-events: none; /* Allow interactions to pass through */
  animation-name: move-bar;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  /* The animationDuration is set inline via the style prop */
}

.real-bar {
  width: 2px;
  background: #00ff0a;
  height: 100%;
  position: absolute;
  left: 0;
}

.moving-bar.animate {
  animation: move-bar forwards;
}

/* Keyframes for moving the bar from left to right */
@keyframes move-bar {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(100%);
  }
}
