/* Controls.css */
.controls {
  margin-left: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  min-width: 200px;
}

.control-item {
  width: 100%;
  max-width: 500px;
  text-align: center;
  margin-bottom: 13px;
  margin-top: 13px;
  display: flex;
  flex-direction: column;
}

.control-item label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

/* Override rc-slider styles for better appearance */

.rc-slider-rail {
  height: 8px;
}

.rc-slider-track {
  height: 8px;
  border-radius: 4px;
}

.rc-slider-handle {
  width: 20px;
  height: 20px;
  margin-top: -6px; /* Centers the handle vertically */
  border: 2px solid #fff;
  background-color: #fff;
  box-shadow: 0 0 0 2px #1890ff;
}

.rc-slider-handle:hover {
  border-color: #40a9ff;
}

.rc-slider-handle:focus {
  box-shadow: 0 0 0 5px rgba(24, 144, 255, 0.2);
}

.rc-slider-mark-text {
  font-size: 12px;
  color: #999;
}

.rc-slider-mark-active {
  color: #1890ff;
}
