/* SoundVisualizer.css */

.sound-visualizer-container {
    width: 100%;
    max-width: 600px; /* Adjust as needed */
    margin: 0 auto;
  }
  
  .controls {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    min-height: 100px;
  }
  
  .controls label {
    margin: 0 10px;
    font-size: 16px;
  }
  
  .chart-container {
    width: 100%;
    height: 300px; /* Increase height for a larger graph */
  }
  