/* ControlPanel.css */

.control-panel {
    display: flex;
    flex-direction: column;
    gap: 1rem; /* Space between control items */
    padding: 1rem;
    background-color: #f9f9f9; /* Light background for better visibility */
    border: 1px solid #ddd;
    border-radius: 8px;
    max-width: 400px;
    margin: auto; /* Center the control panel */
    min-width: 400px;
  }
  
  .control-item {
    display: flex;
    align-items: center;
  }
  
  .control-label {
    display: flex;
    align-items: center;
    font-weight: 500;
    margin-right: 1rem;
  }
  
  .control-label input[type="checkbox"] {
    margin-right: 0.5rem;
    transform: scale(1.2); /* Make the checkbox slightly larger */
  }
  
  .control-slider {
    flex: 1; /* Take up remaining space */
    margin-right: 0.5rem;
  }
  
  .slider-value {
    width: 50px;
    text-align: right;
    font-size: 0.9rem;
    color: #333;
  }
  
  /* Optional: Add hover and focus effects for better UX */
  .control-label:hover {
    cursor: pointer;
  }
  
  .control-slider:hover {
    cursor: pointer;
  }
  